
import { Weather } from '@/store/types'
import { defineComponent } from 'vue'
import type { PropType } from 'vue'

export default defineComponent({
  name: 'WeatherItem',
  props: {
    weather: {
      type: Object as PropType<Weather>,
      required: true,
    },
  },
  data() {
    return {
      iconName: 'cloud',
      img: 'Clouds.svg',
    }
  },
  methods: {
    getIcon() {
      switch (this.weather.weather[0].main) {
        case 'Clouds':
          this.img = 'Clouds.svg'
          this.iconName = 'cloud'
          break
        case 'Clear':
          this.img = 'Clear.svg'
          this.iconName = 'sun'
          break
        case 'Snow':
          this.img = 'Snow.svg'
          this.iconName = 'cloud-snow'
          break
        case 'Rain':
          this.img = 'Rain.svg'
          this.iconName = 'cloud-rain'
          break
        case 'Drizzle':
          this.img = 'Rain.svg'
          this.iconName = 'cloud-drizzle'
          break
        case 'Thunderstorm':
          this.img = 'Thunderstorm.svg'
          this.iconName = 'cloud-lightning'
          break
        default:
          this.img = 'Smoke.svg'
          this.iconName = 'windy'
          break
      }
    },
  },
  mounted() {
    this.getIcon()
  },
  beforeUpdate() {
    this.getIcon()
  },
})
